
body {
  font-family: Avenir,Helvetica,Arial,sans-serif;
  text-align: center;
  color: #2c3e50;
  font-size: 14px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  min-width: 300px;
}
a {
  color: #42b983;
  cursor: pointer;
}
.game {
  width: 100%;
  max-width: 400px;
  overflow: hidden;
  margin: 0 auto;
}
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 0;
}
ul.col {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
li.rect {
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-radius: 2px;
  background-color: #97D5E0;
}
li.rect div {
  width: 100%;
  padding-bottom: 100%;
}
li.rect.filled {
  background-color: #5587A2;
}
ul.col li {
  display: inline-block;
}
.controls {
  margin-top: 20px;
}
a.button {
  padding: 0.75em 2em;
  border-radius: 2em;
  display: inline-block;
  color: #fff;
  background-color: #4fc08d;
  transition: all 0.15s ease;
  box-sizing: border-box;
  border: 1px solid #4fc08d;
}
a.button.white {
  background-color: #fff;
  color: #42b983;
}



